@media (min-width: 320px) and (max-width: 750px) {
  .tab-content {
    overflow-y: scroll;
    margin-left: 0 !important;
  }
  .mat-card.card {
    padding: 10px !important;
  }
  .main-panel .main-content .content-wrapper {
    padding: 0 15px !important;
  }
  th.mat-header-cell,
  td.mat-cell,
  td.mat-footer-cell {
    padding: 0px 10px !important;
  }
  .mat-card-content {
    overflow-y: scroll !important;
  }
  .exam-container .mat-card {
    margin: 10px 0 !important;
    padding: 5px !important;
  }
  .mat-tab-body-content {
    padding: 0;
  }
  .vertical-overlay-menu .wrapper {
    top: 20px !important;
  }
  .horizontal-layout .chat-application {
    margin-top: 3rem;
  }
  .horizontal-layout .chat-application .chat-app-window {
    height: calc(var(--vh, 1vh) * 100 - 20.23rem);
  }
  .header-navbar .navbar-container ul.navbar-nav .dropdown-language {
    margin-top: 0.2rem !important;
  }
  .main-panel .main-content .content-wrapper {
    margin-top: 75px;
  }
  .bannermain .swiper-slide img {
    height: 250px;
  }
  .d-xs-none {
    display: none !important;
  }
  .alert,
  .alert h5 {
    font-size: 14px;
  }
  .user-profile-text {
    margin-top: 0;
    top: calc(50% - 45px);
    bottom: auto;
  }
  .user-profile-buttons {
    padding-top: 3rem;
  }
  .page-user-profile .user-profile-text h4 {
    font-size: 18px;
  }
  /* .cdk-overlay-pane {
    max-width: 90vw !important;
    min-width: 90vw !important;
    max-height: 90vh !important;
  } */
  .announcecar .carousel.slide .card .card-body .card-text {
    display: block;
    max-height: 200px;
    overflow-y: scroll;
  }
  .ann-imgf {
    height: 300px !important;
  }
}

